import { Component, OnInit } from '@angular/core';
import { Model } from 'src/app/api/models/model';
import { RequestService } from 'src/app/api/request.service';
import { UtilService } from 'src/app/services/util.service';
import * as moment from 'moment';
import { DialogService } from 'src/app/api/util/dialog.service';
import { ModalController } from '@ionic/angular';
import { ModalCreateGamePage } from '../../game/modal-create-game/modal-create-game.page';
import { AuthUserService } from 'src/app/services/auth-user.service';
import { CreateGameAdminPage } from '../../game/create-game-admin/create-game-admin.page';
import { UtilArrayService } from 'src/app/services/util-array.service';
import { ActivatedRoute } from '@angular/router';
import { ListsHomeService } from 'src/app/services/lists-home.service';


@Component({
  selector: 'app-games',
  templateUrl: './games.page.html',
  styleUrls: ['./games.page.scss'],
})
export class GamesPage implements OnInit {

  public modelGame  : Model;
  //public list = [];
  public  tabs = []
  public tabSelected :any;
  public lastFocusSegment : any;

  public listSkeleton = new Array(3)
  public firstLoad = true;

  constructor(
    public request : RequestService,
    public util : UtilService,
    public dialogService : DialogService,
    public modalCtrl : ModalController,
    public authUser : AuthUserService,
    public utilArray : UtilArrayService,
    public route : ActivatedRoute,
    public listsHomeService : ListsHomeService
  ) {
    this.modelGame = new Model('Game',request);

   }

  ngOnInit() {
    this.init();


    setTimeout(()=>{this.analyzeParameters()}, 1000);

    //this.analyzeParameters();
  }

  async init(event = null){
    /*this.modelGame.api_function('games').subscribe(data => {
      if(data['status'] = 'success'){
        this.list = data['data'];
        if(event)
          event.target.complete();
      }
    })
    */
  
    this.modelGame.api_function('pageHomeGames').subscribe(
      response => {
        if(response['status'] == 'success'){
          this.tabs = response['data']['dates'];
          this.selectFirst();
        }
        if(event)
          event.target.complete()
        console.log(response)
        this.firstLoad = false;

      },
      error => {
        if(event)
          event.target.complete()
        console.error(error)
        this.firstLoad = false;

      }
    )

  }

  async selectFirst(){
    if(this.tabs.length > 0){
      let tab = this.tabs[0];
      this.listsHomeService.games = tab['results']
      this.tabSelected = 0
      await this.delay( 1500 )
      this.focusSegment()
      //this.selectDate(tab['date']);
    }
  }

  async selectDate(event){
    let index = parseInt( event.detail.value );

    let tab = this.tabs[index];

    if(tab && tab['results']){
      this.listsHomeService.games = tab['results']
      this.focusSegment()
      return;
    }
    await this.dialogService.loadingProgres();

    this.modelGame.api_function('gamesByDate',{'date' : tab['date']}).subscribe(
      response => {
        if(response['status'] == 'success'){
          this.tabs[index]['results'] = response['data']
          this.listsHomeService.games = this.tabs[index]['results'];
          this.focusSegment()
        }
        this.dialogService.dimissLoaging();
        console.log(response);
      },
      error => {
        console.error(error);
        this.dialogService.dimissLoaging();

      }
    )

  }

  focusSegment() {
    let segmentId = 'sgm_' + this.tabSelected;
    console.log('select segment : ', segmentId)
    document.getElementById(segmentId).scrollIntoView({ 
      behavior: 'smooth',
      block: 'center',
      inline: 'center'
    });
  }

  
  delay(ms: number) {
    return new Promise( resolve => setTimeout(resolve, ms) );
  }

  formatDate(val){
    return moment(val).locale('es').format('DD MMMM YYYY')
  }

  async addGame(){

    console.log('date' , this.tabs[ this.tabSelected ] )
    let date = this.tabs[this.tabSelected] ? this.tabs[this.tabSelected]['date'] : ''; 

    let modal = await this.modalCtrl.create({
      //component: ModalCreateGamePage
      component: CreateGameAdminPage,
      componentProps:{
        date : date
      }
    })

    modal.onDidDismiss().then(data=>{
      console.log('crate game')
      if(data.data && data.data['game']){
        const game = data.data['game'];
        let date = this.util.setFormatMoment(game.date,'YYYY-MM-DD');
        let index = this.tabs.map( tab => tab.date ).indexOf(date)
        if(index >= 0){
          this.utilArray.listAddFirst( this.tabs[index]['results'] ,game );
        }else{
          index = this.tabs.length;
          this.tabs[index] = { date : date ,results : [] } 
          this.utilArray.listAddFirst( this.tabs[index]['results'] ,game );
        }
        this.tabSelected = index;
        this.focusSegment();
      }
      
    })

    return await modal.present();
  }

  analyzeParameters(){
    console.log('run analyzeParameters')
    if(this.route)
      if(this.route.snapshot.queryParamMap.get('create'))
        this.addGame();
      
  }
 




  ionViewWillEnter(){
    console.log('ionViewWillEnter');
  }	// Fired when the component routing to is about to animate into view.
  ionViewDidEnter()
  {
    console.log('ionViewDidEnter');
  }	// Fired when the component routing to has finished animating.  
  ionViewWillLeave(){
    console.log('ionViewWillLeave');
  }	// Fired when the component routing from is about to animate.
  ionViewDidLeave(){
    console.log('ionViewDidLeave');
  }


}
